
import axios from 'axios';
import wx from 'weixin-js-sdk';
import weixin from '@/services/weixin';


import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Alert from 'vux/src/components/alert/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Picker from 'vux/src/components/picker/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'


export default {
directives:{
TransferDom
},
components: {
XHeader,
Popup,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
DatetimePlugin,
Datetime,
Group,
Picker,
InlineXNumber,
Alert,

},
data() {
return {
Messge13: '',
Messge14: '',
Messge15: '',
itemscore: 0,
showPop:false,
contents: { rights: '评分说明', title: '先锋作用评定' },
picList15: { list: [], arr: [] },
picList14: { list: [], arr: [] },
picList13: { list: [], arr: [] }
};
},
computed: {
userName() {
return decodeURIComponent(this.$route.params.name);
}
},
methods: {
submit() {
let { departmentId, userId, partmentId } = this.$route.params;
let { Messge13, Messge14, Messge15, itemscore } = this;
let obj = {
Messge13,
Messge14,
Messge15,
itemscore,
departmentid: +departmentId,
userid: +userId,
partmentid: +partmentId
};
const empty1 = !this.Messge13 && !this.picList13.arr.length;
const empty2 = !this.Messge14 && !this.picList14.arr.length;
const empty3 = !this.Messge15 && !this.picList15.arr.length;
if (empty1 && empty2 && empty3) {
return this.$vux.toast.show({
text: '请填写评定内容并上传凭证',
type: 'text'
});
}
if (!empty1) {
if (!this.Messge13) {
return this.$vux.toast.show({
text: '请填写获得荣誉评定内容',
type: 'text'
});
}
if (!this.picList13.arr.length) {
return this.$vux.toast.show({
text: '请上传荣誉凭证',
type: 'text'
});
}
obj.pic13 = this.picList13.arr.join(',');
}
if (!empty2) {
if (!this.Messge14) {
return this.$vux.toast.show({
text: '请填写先锋表彰评定内容',
type: 'text'
});
}
if (!this.picList14.arr.length) {
return this.$vux.toast.show({
text: '请上传表彰凭证',
type: 'text',
position: 'center'
});
}
obj.pic14 = this.picList14.arr.join(',');
}
if (!empty3) {
if (!this.Messge15) {
return this.$vux.toast.show({
text: '请填写先锋模范评定内容',
type: 'text'
});
}
if (!this.picList15.arr.length) {
return this.$vux.toast.show({
text: '请上传模范凭证',
type: 'text',
position: 'center'
});
}
obj.pic15 = this.picList15.arr.join(',');
}
axios({
url: 'pavantgrade/save',
method: 'post',
headers: { contentType: 'application/json' },
params: obj
})
.then(res => {
if (res.success) {
this.$vux.alert.show({
title: '提交成功',
onHide(){
setTimeout(() => history.back(), 500);
}
});
} else {
this.$vux.alert.show({ title: res.msg });
}
})
.catch(err => {
this.$vux.alert.show({ title: '提交失败' });
});
},
chooseImage(it) {
wx.chooseImage({
count: 1, // 默认9
sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: res => {
let localIds = res.localIds || [];
new Promise(resolve => {
let serverIds = [];
let toUpload = localId =>
wx.uploadImage({
localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
isShowProgressTips: 1, // 默认为1，显示进度提示
success: res => {
serverIds.push(res.serverId);
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}
});
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}).then(serverIds => {
let promiseList = [];
serverIds.map(serverId =>
promiseList.push(
this.$http.get('picture/upload', {
params: {
mediaId: serverId
}
})
)
);
Promise.all(promiseList).then(result => {
let pictureIds = [];
result.map(item => pictureIds.push(item.data));
it.list.push('http://dj.dlbdata.cn/dangjian/picture/showThumbnail?pictureId=' + pictureIds.join());
it.arr.push(pictureIds.join());
});
});
}
});
},
know(){
this.showPop = false
},
showPops(){
this.showPop = !this.showPop;
}
},
mounted() {
weixin.init(['chooseImage', 'uploadImage']);
}
};



